import store from "@/store";
import { createRouter, createWebHashHistory, RouteRecordRaw } from "vue-router";
import FilterRouters from "./FilterRouters";

let routes: Array<RouteRecordRaw> = []; // 主路由表
const globalRouters = store.state.fovcommon.globalRouters;
console.log("全局动态路由", globalRouters);
if (globalRouters.length > 0) {
  // 加载后端路由表
  globalRouters.forEach((g: RouteRecordRaw) => {
    routes.push(g);
  });
} else {
  // 加载内置默认路由
  const modulesFiles = require.context("./modules", true, /\.ts$/);
  modulesFiles.keys().forEach((mf) => {
    const value: Array<RouteRecordRaw> | RouteRecordRaw = modulesFiles(mf);
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    const obs: any = value;
    const obj = obs.default;
    if (obj instanceof Array) {
      obj.forEach((v) => {
        routes.push(v);
      });
    } else {
      const oba = obj as RouteRecordRaw;
      routes.push(oba); // 暂时不处理包含子路由的
      // console.log("暂时不处理包含子路由:", obj);
    }
  });
}
// console.log("当前路由", routes);
routes = FilterRouters(routes); // 路由拦截
const router = createRouter({
  // history: createWebHistory(process.env.BASE_URL),
  history: createWebHashHistory(),
  routes,
});

export default router;
