/* eslint-disable @typescript-eslint/no-explicit-any */
import { RouteRecordRaw } from "vue-router";

const state = {
  // global
  globalRouters: [], // 全局路由,获取全局路由,可由后端控制,为空则使用默认配置
  globalPaths: [], // 全局路径,获取全局可访问路径,可由后端控制,为空则使用默认配置
  globalPermissions: [], // 全局权限,获取全局权限字段,可由后端控制,为空则使用默认配置
  // base
};
const mutations = {
  setGlobalRouters(state: any, value: Array<RouteRecordRaw>) {
    state.globalRouters = value;
  },
  setGlobalPaths(state: any, value: Array<string>) {
    state.globalPaths = value;
  },
  setGlobalPermissions(state: any, value: Array<string>) {
    state.globalPermissions = value;
  },
};
const actions = {};
const getters = {};
export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};
