import { RouteRecordRaw } from "vue-router";
/**
 * 路由整合
 * 将分路由整合到路由表中
 * @param routersArr 新路由
 */
export function MergeRouterArr(
  ChildrenRouter: Array<RouteRecordRaw>
): Array<string> {
  const RouterPathTemp: Array<string> = []; // 临时路由表,用于合并至主路由
  ChildrenRouter.forEach((child) => {
    ParseRouterStr(child).forEach((p) => {
      RouterPathTemp.push(p);
    });
  });
  return RouterPathTemp;
}

/**
 * 解析单个路由
 * @param {RouteRecordRaw} child
 * @return {*}  {Array<string>}
 */
export function ParseRouterStr(child: RouteRecordRaw): Array<string> {
  const RouterStrTemp: Array<string> = []; // 临时路由表,用于合并至主路由
  if (child.children != undefined && child.children.length > 0) {
    // RouterPathTemp.push(children.path); // 父节点添加至暂存器
    child.children.forEach((c) => {
      RouterStrTemp.push(
        child.path +
          "/" +
          c.path.split("/")[c.path.split("/").length - 1].replace(/ /g, "/")
      );
    });
  }
  RouterStrTemp.push(child.path);
  return RouterStrTemp;
}
