// eslint-disable-next-line @typescript-eslint/no-explicit-any
const W: any = window;
export const NotificationSuccessHandler = (notice: string): void => {
  W.$notice.success(notice);
};

export const NotificationInfoHandler = (notice: string): void => {
  W.$notice.info(notice);
};

export const NotificationErrorHandler = (notice: string): void => {
  W.$notice.error(notice);
};

/**
 * 创建基础的消息提示,如需执行方法,请直接重写组件
 * @param title
 * @param notice
 * @param times
 * @param closable
 */
export const NotificationNewHandler = (
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  title: string | any,
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  notice: string | any,
  times: number,
  closable: boolean
): void => {
  W.$notice.create({
    title: title,
    content: notice,
    duration: times,
    closable: closable,
  });
};
