import store from "@/store";
import { RouteRecordRaw } from "vue-router";
import { MergeRouterArr, ParseRouterStr } from "./MergeRouterArr";

/**
 *拦截路由-将分路由拦截
 *
 * @param {Array<RouteRecordRaw>} routers
 * @return {*}  {Array<RouteRecordRaw>}
 */
function FilterRouters(routers: Array<RouteRecordRaw>): Array<RouteRecordRaw> {
  const rs = routers;
  const canRs: Array<RouteRecordRaw> = [];
  let canRouterStr: Array<string> = [];
  const globalPaths = store.state.fovcommon.globalPaths;
  console.log("全局可访问路由:", globalPaths);
  if (globalPaths.length > 0) {
    // 获取到的可访问路由,此处可根据设定的路由决定哪些页面能访问,全局且动态加载(store)
    canRouterStr = globalPaths;
  } else {
    canRouterStr = MergeRouterArr(rs); // 预设的所有路由
  }
  rs.forEach((r) => {
    if (r.children == null || r.children.length == 0) {
      // 无子节点的路由对象
      if (canRouterStr.includes(ParseRouterStr(r)[0])) {
        canRs.push(r);
      }
    } else {
      // 有子节点的路由对象 == 父节点正常, 子节点移除, 所有子节点都移除则顺带移除父节点
      const obr: Array<RouteRecordRaw> = [];
      r.children.forEach((o) => {
        if (canRouterStr.includes(ParseRouterStr(r)[0])) {
          obr.push(o);
        }
      });
      if (obr.length > 0) {
        r.children = obr;
        canRs.push(r);
      }
    }
  });
  console.log("处理好的路由:", canRs);
  return canRs;
}

export default FilterRouters;
