/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
"use strict";

import router from "@/router";
import axios from "axios";
import { Config } from "@/settings";
import { getToken } from "@/utils/auth";
import Cookies from "js-cookie";
import store from "@/store";
import { NotificationNewHandler } from "@/components/Common/Msg/NotificationApi/Notification";

const config = {
  baseURL:
    process.env.NODE_ENV === "production" ? process.env.VUE_APP_BASE_API : "/", // api 的 base_url
  timeout: Config.timeout, // 请求超时时间
  // withCredentials: true, // Check cross-site Access-Control
};

const _axios = axios.create(config);

_axios.interceptors.request.use(
  function (config: any) {
    if (getToken()) {
      config.headers["Authorization"] = getToken(); // 让每个请求携带自定义token 请根据实际情况自行修改
    }
    config.headers["Content-Type"] = "application/json";
    console.log("[请求]", JSON.stringify(config));
    return config;
  },
  function (error) {
    // Do something with request error
    return Promise.reject(error);
  }
);

// Add a response interceptor
_axios.interceptors.response.use(
  function (response) {
    // Do something with response data
    console.log("[结果]", JSON.stringify(response));
    return response.data;
  },
  function (error) {
    // Do something with response error
    // 兼容blob下载出错json提示
    if (
      error.response.data instanceof Blob &&
      error.response.data.type.toLowerCase().indexOf("json") !== -1
    ) {
      const reader = new FileReader();
      reader.readAsText(error.response.data, "utf-8");
      // eslint-disable-next-line @typescript-eslint/no-unused-vars
      reader.onload = function (e) {
        const r: any = reader.result;
        const errorMsg = JSON.parse(r).message;
        NotificationNewHandler("异常提醒", errorMsg, 5000, false);
      };
    } else {
      let code = 0;
      try {
        code = error.response.data.status;
      } catch (e) {
        if (error.toString().indexOf("Error: timeout") !== -1) {
          NotificationNewHandler("异常提醒", "网络请求超时", 5000, false);
          return Promise.reject(error);
        }
      }
      console.log(code);
      if (code) {
        if (code === 401) {
          store.dispatch("LogOut").then(() => {
            // 用户登录界面提示
            Cookies.set("point", "401");
            location.reload();
          });
        } else if (code === 403) {
          router.push({ path: "/401" });
        } else {
          const errorMsg = error.response.data.message;
          if (errorMsg !== undefined) {
            NotificationNewHandler("异常提醒", errorMsg, 5000, false);
          }
        }
      } else {
        NotificationNewHandler("异常提醒", "接口请求失败", 5000, false);
      }
    }
    return Promise.reject(error);
  }
);

export default {
  install: function (app: any, options: any) {
    options;
    // console.log(options);
    // 添加全局的方法
    app.config.globalProperties.axios = _axios;
    // 添加全局的方法
    app.config.globalProperties.$translate = (key: any) => {
      return key;
    };
  },
};
