import FovView from "@/views/FovView.vue";
import { RouteRecordRaw } from "vue-router";

/**
 * 公共路由权限无卡关
 * 此处维护路由数据,可直接整合至主路由表
 */
const CommonRouters: Array<RouteRecordRaw> = [
  {
    path: "/",
    name: "common",
    component: FovView,
    redirect: "/home",
    meta: { description: "通用" },
    children: [
      {
        path: "home",
        name: "home",
        component: () => import("@/views/HomePane.vue"),
        meta: { description: "公共" },
      },
      {
        path: "about",
        name: "about",
        component: () => import("@/views/Author/About/AboutView.vue"),
        meta: { description: "关于页面" },
      },
      {
        path: "404",
        name: "404PageNotFound",
        component: () => import("@/views/Fov/Error/404/404.vue"),
        meta: { description: "404" },
      },
      {
        path: "401",
        name: "401PageError",
        component: () => import("@/views/Fov/Error/401/401.vue"),
        meta: { description: "401" },
      },
    ],
  },
];
export default CommonRouters;
